  /* HTML: <div class="loader"></div> */
/* HTML: <div class="loader"></div> */
.loader-sqare {
  width:50px;
  aspect-ratio: 1;
  display: grid;
}
.loader-sqare:before,
.loader-sqare:after {
  content: "";
  grid-area: 1/1;
  --c:#0000 calc(100%/3),#04171f 0 calc(2*100%/3),#22cc9d 0;
  --c1:linear-gradient(90deg,var(--c));
  --c2:linear-gradient( 0deg,var(--c));
  background: var(--c1),var(--c2),var(--c1),var(--c2);
  background-size: 300% 4px,4px 300%;
  background-repeat: no-repeat;
  animation: lsr 1s infinite linear;
}
.loader-sqare:after {
  margin: 10px;
  transform: scaleX(-1);
  animation-delay: -.25s;
}
@keyframes lsr {
  0%   {background-position: 50%  0,100% 100%,0    100%,0 0}
  25%  {background-position: 0    0,100% 50% ,0    100%,0 0}
  50%  {background-position: 0    0,100% 0   ,50%  100%,0 0}
  75%  {background-position: 0    0,100% 0   ,100% 100%,0 50%}
 75.01%{background-position: 100% 0,100% 0   ,100% 100%,0 50%}
  100% {background-position: 50%  0,100% 0   ,100% 100%,0 100%}
} 




/* HTML: <div class="loader"></div> */
 .loader-circle {
  width: 60px;
  aspect-ratio: 1;
  border-radius: 50%;
  animation: l11 1s infinite;
}
@keyframes l11 {
  0%   {background: conic-gradient(rgb(160, 160, 37) 0     ,#0000 0)}
  12.5%{background: conic-gradient(rgb(160, 160, 37) 45deg ,#0000 46deg)}
  25%  {background: conic-gradient(rgb(160, 160, 37) 90deg ,#0000 91deg)}
  37.5%{background: conic-gradient(rgb(160, 160, 37) 135deg,#0000 136deg)}
  50%  {background: conic-gradient(rgb(160, 160, 37) 180deg,#0000 181deg)}
  62.5%{background: conic-gradient(rgb(160, 160, 37)225deg,#0000 226deg)}
  75%  {background: conic-gradient(rgb(160, 160, 37) 270deg,#0000 271deg)}
  87.5%{background: conic-gradient(#04171f 315deg,#0000 316deg)}
  100% {background: conic-gradient(#04171f 360deg,#0000 360deg)}
} 

/* <div class="loader-squarecircle"></div> */
.loader-squarecircle {
  width: 32px;
  height: 16px;
  display: flex;
}
.loader-squarecircle:before,
.loader-squarecircle:after {
  content: "";
  flex: 1;
  background: #3FB8AF;
  transform-origin: top right;
  animation: lsc 2s infinite;
}
.loader-squarecircle:after {
  background: #FF3D7F;
  transform-origin: top left;
  animation-delay: .25s;
}
@keyframes lsc {
   0%,5%    {transform: rotate(0)}
   20%,30%  {transform: rotate(90deg)}
   45%,55%  {transform: rotate(180deg)}
   70%,80%  {transform: rotate(270deg)}
   95%,100% {transform: rotate(360deg)}
}



/* HTML: <div class="loader"></div> */
.loader-CS {
  width: 35px;
  aspect-ratio: 1;
  --c:no-repeat linear-gradient(#046D8B 0 0);
  background:
    var(--c) 0 0,
    var(--c) 100% 0,
    var(--c) 100% 100%,
    var(--c) 0 100%;
  animation: 
    l2-1 2s infinite,
    l2-2 2s infinite;
}
@keyframes l2-1 {
  0%   {background-size: 0    4px,4px 0   ,0    4px,4px 0   }
  12.5%{background-size: 100% 4px,4px 0   ,0    4px,4px 0   }
  25%  {background-size: 100% 4px,4px 100%,0    4px,4px 0   }
  37.5%{background-size: 100% 4px,4px 100%,100% 4px,4px 0   }
  45%,
  55%  {background-size: 100% 4px,4px 100%,100% 4px,4px 100%}
  62.5%{background-size: 0    4px,4px 100%,100% 4px,4px 100%}
  75%  {background-size: 0    4px,4px 0   ,100% 4px,4px 100%}
  87.5%{background-size: 0    4px,4px 0   ,0    4px,4px 100%}
  100% {background-size: 0    4px,4px 0   ,0    4px,4px 0   }
}
@keyframes l2-2 {
  0%,49.9%{background-position: 0 0   ,100% 0   ,100% 100%,0 100%}
  50%,100%{background-position: 100% 0,100% 100%,0    100%,0 0   }
}


